import Vue from "vue";
import VueRouter from "vue-router";
import logor from "../loginA.vue";
import rega from "../registerA.vue";
import axios from "axios";


// import store from '../store'

Vue.use(VueRouter);

const routes = [

  {
    path: '/404',
    name: 'NotFound',
    component: () => import("../views/notFound.vue"),
  },

  {
    path: '*',
    redirect: '/404', // Redirect unmatched routes to the 404 page
  },


  {
    path: "/login",
    name: "login",
    component: logor,
    meta: {
      requireNothing: true,
    },
  },
  {
    path: "/register",
    name: "register",
    component: rega,
    meta: {
      requireNothing: true,
    },
  },

  {
    path: "/verified/:token",
    name: "verified",
    component: () => import(/* webpackChunkName: "about" */ "../views/verifiedPage.vue"),
  },

  {
    path: "/",
    name: "welcome",
    component: () => import(/* webpackChunkName: "about" */ "../views/homePage.vue"),
    meta: {
      description: "Embark on an inspirational journey through our carefully curated blog, where we explore the world's wonders, share stories of triumph, and offer insights that empower you to lead a life filled with purpose and creativity. Join our community of dreamers, doers, and visionaries, and let the magic of inspiration ignite your soul. Your path to a brighter tomorrow begins here."
    },    
    children: [
      {
        path: "family-tree",
        name: "family-tree",
        component: () => import(/* webpackChunkName: "about" */ "../views/aboutMe.vue"),
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: "articles",
        name: "articles",
        component: () => import(/* webpackChunkName: "about" */ "../views/articlesView.vue"),
      },
      {
        path: "profil",
        name: "profil",
        component: () => import(/* webpackChunkName: "about" */ "../views/setUp.vue"),
        props: (route) => ({ email: route.query.email }),
        meta: {
          requiresAuth: true,
        },
      },

      {
        path: "/edit",
        name: "edit",
        component: () => import(/* webpackChunkName: "about" */ "../views/editProfile.vue"),
        meta: {
          requiresAuth: true,
        },
      },

      {
        path: "/article/:slug",
        name: "reading",
        component: () => import(/* webpackChunkName: "about" */ "../views/readingPage.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
  mode: "history",
  caseSensitive: true,

  // caseSensitive: true,

});

router.beforeEach((to, from, next) => {
  // Convert the entire route path to lowercase
  const lowercasePath = to.path.toLowerCase();

  // Check if the path is already lowercase
  if (to.path === lowercasePath) {
    next();
  } else {
    // Redirect to the lowercase version
    next({ path: lowercasePath });
  }
});


router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some((x) => x.meta.requiresAuth);
  const requireNothing = to.matched.some((x) => x.meta.requireNothing);

  const isAuthenticated = JSON.parse(localStorage.getItem("token"));
  console.log(router);

  if (requiresAuth && !isAuthenticated) {
    next("/login");
  } else if (requireNothing && isAuthenticated) {
    next({ name: "profil" || "edit" || "family-tree"});
  } else {
    next();
  }
});

router.beforeEach((to, from, next) => {
  // Check if the route has a slug parameter
  if (to.params.slug) {
    // Perform an Axios request to check if the article exists
    axios
      .get(`https://andrewoethara.my.id/get_post.php?slug=${to.params.slug}`)
      .then(() => {
        // The article exists, continue to the route
        next();
      })
      .catch(() => {
        // The article does not exist, redirect to the 404 page
        next({name: "NotFound"});
      });
  } else {
    // If the route doesn't have a slug parameter, continue to the route
    next();
  }
});


export default router;
